/** VARS **/
$whatsapp-color: #25d366;
$whatsapp-color-hover: #000000;

/** CSS **/
html[dir="rtl"],
body.rtl {
	.webing-whatsapp-button {
		a {
			&.has-text {
				img {
					margin-right: 0;
					margin-left: 10px;
				}
			}
		}
	}
}

.webing-whatsapp-button {
	position: fixed;
	bottom: 10px;
	z-index: 9999;

	&.align_left {
		left: 10px;
	}

	&.align_right {
		right: 10px;
	}

	a {
		display: inline-block;
		background-color: $whatsapp-color;
		transition: all .3s ease;
		color: #ffffff;
		padding: 15px;
		border-radius: 50%;

		&:hover,
		&:focus {
			background-color: $whatsapp-color-hover;
		}

		&.has-text {
			display: flex;
			align-items: center;
			border-radius: 25px;

			img {
				width: 24px;
				height: 24px;
				margin-right: 10px;
			}
		}

		img {
			display: block;
			width: 36px;
			height: 36px;
		}

		span {
			color: #ffffff;
			font-size: 16px;
			display: block;
			line-height: 1;
		}
	}
}